.searchbar {
    top: 0;
    position: sticky;
    
    z-index: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 64px;
  
    padding-right: 24px;
    padding-left: 24px;
    padding-top: 12px;
    padding-bottom: 12px;
  
    color: #fff;
    background-color: #f90303;
    box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2),
      0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
  }
  
  .Form {
    display: flex;
    align-items: center;
    width: 100%;
    max-width: 600px;
    background-color: #fff;
    border-radius: 3px;
  }
  
  .Button {
    display: inline-block;
    width: 48px;
    height: 48px;
    border: 0;
    background-size: 40%;
    background-repeat: no-repeat;
    background-position: center;
    opacity: 0.6;
    transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
    background-color: #964343;
    cursor: pointer;
    outline: none;
  }
  
  .Button:hover {
    opacity: 1;
  }
  
  .Input {
    display: inline-block;
    width: 100%;
    font: inherit;
    font-size: 20px;
    border: none;
    outline: none;
    padding-left: 4px;
    padding-right: 4px;
  }
  
  .Input::placeholder {
    font: inherit;
    font-size: 18px;
  }